.header {
  position: relative;
  z-index: 3;
  max-width: 100%;
  height: 74px;
  line-height: 74px;
  color: $color-white;
  background: #1c1c1c;

  @media (min-width: 861px) {
    padding: 20px 0;
    height: auto;
    line-height: normal;
    color: $color-black;
    background: $color-alabaster;

    .body-page404 & {
      color: $color-white;
      background: $body-background;
    }
  }

  @media (min-width: 861px) and (max-width: 1024px) {
    padding: 15px 0;
  }

  @media (min-width: 1025px) {
    padding: 24px 0;
  }

  &__inner {
    display: none;

    @media (min-width: 861px) {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__hamburger {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    height: 74px;

    @media (min-width: 861px) {
      display: none;
    }
  }

  &__mobile-logo {
    display: inline-block;
    vertical-align: middle;
    padding-left: 74px;
    max-width: 100%;

    @media (min-width: 769px) {
      padding-left: 120px;
    }

    @media (min-width: 861px) {
      display: none;
    }
  }

  &__menu {
    font-size: 1.4rem;
    line-height: 1.2;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    li {
      display: inline-block;
      vertical-align: middle;
      margin: 0;
      padding: 3px 10px;

      @media (min-width: 1024px) and (max-width: 1400px) {
        padding: 5px 25px;
      }

      @media (min-width: 1401px) {
        padding: 8px 40px;
      }

      ul {
        display: none;
      }
    }

    a {
      opacity: 0.5;
      text-decoration: none;
      transition: opacity 0.2s ease;
    }

    li.is-active a,
    a:hover {
      opacity: 1;
    }
  }

  &__info {
    display: table;

    .body-page404 & {
      visibility: hidden;
      opacity: 0;
    }
  }

  &__link,
  &__lang-bar {
    display: table-cell;
    vertical-align: middle;
    padding: 0 5px;

    @media (min-width: 1401px) {
      padding: 0 13px;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  &__lang-bar {
    padding-left: 8px;

    @media (min-width: 1401px) {
      padding-left: 23px;
    }
  }
}