.product {
  padding: 40px 0;

  @media (min-width: 821px) {
    display: table;
    table-layout: fixed;
    padding-top: 45px;
    padding-bottom: 25px;
    width: 100%;
  }

  @media (max-width: 1780px) {
    position: relative;
  }

  &__prev,
  &__next {
    display: none;
    position: absolute;
    top: 30vh;
    z-index: 10;
    margin-top: -15px;
    text-decoration: none;

    @media (min-width: 769px) {
      display: block;
    }
    @media (min-width: 1781px) {
      top: 52vh;
    }

    svg {
      display: inline-block;
      vertical-align: middle;
      width: 20px;
      height: 30px;
      stroke: #7e7e7e;

      @media (max-width: 1024px) {
        &:nth-child(2),
        &:nth-child(3) {
          display: none;
        }
      }

      @media (min-width: 1025px) {
        stroke: $color-silver;
      }
    }

    svg + svg {
      margin-left: -5px;
    }

    &.is-disabled {
      cursor: not-allowed;

      svg {
        opacity: 1;
        stroke: $color-gallery;
      }

      svg:nth-child(2),
      svg:nth-child(3) {
        display: none;
      }
    }
  }

  &__prev {
    left: -15px;

    svg:nth-child(2) {
      opacity: 0.5;
    }

    svg:nth-child(3) {
      opacity: 0.1;
    }

    @media (min-width: 1781px) {
      left: 50px;
    }
  }

  &__next {
    right: -15px;

    @media (min-width: 1025px) {
      svg {
        opacity: 0.1;
      }

      svg:nth-child(2) {
        opacity: 0.5;
      }

      svg:nth-child(3) {
        opacity: 1;
      }
    }

    @media (min-width: 1781px) {
      right: 50px;
    }
  }

  &__column {
    @media (min-width: 821px) {
      display: table-cell;
      vertical-align: top;
    }

    @media (min-width: 1025px) {
      vertical-align: middle;
    }

    &_images {
      @media (min-width: 821px) and (max-width: 1024px) {
        width: 58%;
        padding-right: 20px;
      }

      @media (min-width: 1025px) {
        width: 68%;
        padding-right: 40px;
      }
    }

    &_content {
      position: relative;
      z-index: 2;
      padding-top: 50px;

      @media (min-width: 821px) and (max-width: 1024px) {
        width: 42%;
      }

      @media (min-width: 1025px) {
        padding-top: 80px;
        width: 32%;
      }
    }
  }

  &__title {
    margin: 0 0 1em;
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.1;
    text-transform: uppercase;
    color: $color-white;
  }

  &__descr {
    max-width: 320px;
    font-size: 1.4rem;
    line-height: 1.7;
    color: $color-white;
  }

  &__tocart {
    margin: 40px 0 0;

    @media (min-width: 1025px) and (max-width: 1440px) {
      margin: 60px 0 0;
    }

    @media (min-width: 1441px) {
      margin: 95px 0 0;
    }
  }

  &__options {
    &-title {
      margin-bottom: 20px;
      font-weight: 400;
      font-size: 1.2rem;
      line-height: 1.2;
      text-transform: uppercase;
      color: $color-white;
    }

    &-values {

    }
  }

  &__size {
    display: inline-block;
    vertical-align: middle;
    margin: 0 25px 0 0;
    padding: 5px;

    &:last-child {
      margin-right: 0;
    }

    label {
      position: relative;

      input {
        position: absolute;
        visibility: hidden;
        opacity: 0;
      }

      span {
        opacity: 0.6;
      }

      &:hover span,
      input:checked + span {
        opacity: 1;
      }
    }
  }

  &__prices {
    margin: 40px 0 25px;

    @media (min-width: 1025px) and (max-width: 1440px) {
      margin: 50px 0 30px;
    }

    @media (min-width: 1441px) {
      margin: 80px 0 65px;
    }
  }

  &__price {
    display: inline-block;
    vertical-align: middle;
    margin-right: 30px;
    font-size: 1.6rem;
    line-height: 1.2;
    color: $color-white;
    
    &_old {
      text-decoration: line-through;
    }
  }

  &__btn {

  }
}