/*template*/
.all-wrap {
  display: table;
  table-layout: fixed;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.header-wrap,
.main-wrap,
.footer-wrap {
  display: table-row;
  width: 100%;
}

.main-wrap {
  position: relative;
  z-index: 2;
  height: 95%;
}

.header-wrap,
.footer-wrap {
  height: auto;
  min-height: 5%;
}

.footer-wrap {
  position: relative;
  z-index: 2;
}

.body-full-height {
  .main-wrap::after,
  .content-wrap {
    display: inline-block;
    vertical-align: middle;
  }

  .main-wrap {
    text-align: center;

    &::after {
      content: "";
      height: 100%;
      width: 1px;
    }
  }

  .content-wrap {
    text-align: left;
  }
}

.body-page404 {
  .main-wrap::after,
  .content-wrap {
    vertical-align: bottom;
  }
}

/*    */
.body-bg-floid .all-wrap {
  position: relative;

  &::after {
    content: "";
    display: none;
    position: fixed;
    left: 65%;
    top: 105px;
    opacity: 0.1;
    width: 30%;
    height: 100%;
    background: url("../images/bg_floid-grey.png") no-repeat right bottom;
    background-size: contain;

    @media (min-width: 1025px) {
      display: block;
      left: 50.5%;
      width: 45%;
    }
  }
}

/*end template*/

/* content */
.content {
  padding: 20px 0;

  @media (min-width: 1025px) and (max-width: 1440px) {
    padding: 20px 0 45px;
  }

  @media (min-width: 1441px) {
    padding: 20px 0 65px;
  }

  .body-bg-floid & {
    padding-top: 35px;

    @media (min-width: 1025px) and (max-width: 1200px) {
      padding-top: 60px;
      max-width: 80%;
    }

    @media (min-width: 1201px) {
      padding-top: 100px;
      max-width: 60%;
    }
  }
}

/* form */
.alert {
  display: block;
  color: #fff;
  font-weight: 500;
  margin-bottom: 1em;
  padding: 0.5em 1em;
  width: 100%;

  &-error {
    background: #ce1d1d;
  }

  &-success {
    background: #37a13c;
  }
}

.recaptcha {
  margin-bottom: 20px;
}

.input-groups {
  padding: 0 35px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-flow: row wrap;
  width: 100%;
}

.input-group {
  flex-basis: 27.5%;
  padding: 0 35px;

  &_big {
    flex-grow: 2;
  }
}

.input {
  margin: 0 auto 15px;

  &_error {
    input, textarea {
      border-color: #ce1d1d
    }
  }
}

.input-text,
select,
.input-textarea {
  border: 0 none;
  border-radius: 10px;
  padding: 0 26px;
  width: 100%;
  font-family: inherit;
  font-size: 1.4rem;
  letter-spacing: -0.28px;
  background: $color-mine-shaft;
  color: $input-color;
  transition: border-color 0.2s;

  .black-inputs & {
    background: $input-background;
  }

  &:focus {
    border-color: $input-border-color-focus;
  }

  &::-webkit-input-placeholder {
    color: $placeholder-color;
    font-family: inherit;
    opacity: 1;
  }

  &::-moz-placeholder {
    color: $placeholder-color;
    font-family: inherit;
    opacity: 1;
  }

  &:-moz-placeholder {
    color: $placeholder-color;
    font-family: inherit;
    opacity: 1;
  }

  &:-ms-input-placeholder {
    color: $placeholder-color;
    font-family: inherit;
    opacity: 1;
  }
}

.input-placeholder {
  display: inline-block;
  vertical-align: top;
  position: relative;
  width: 100%;

  .is-value &__input,
  &__input:focus {
    padding-top: 10px;
  }

  .is-value &__input.input-textarea,
  &__input.input-textarea:focus {
    padding-top: 20px;
  }

  &__text {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: hidden;
    padding: 0 26px;
    max-width: 100%;
    font-size: 1.4rem;
    line-height: 40px;
    letter-spacing: -0.28px;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: $placeholder-color;
    pointer-events: none;
    transition: all 0.3s ease;

    @media (min-width: 1201px) {
      line-height: 50px;
    }
  }

  .is-value &__text,
  &__input:focus + &__text {
    bottom: auto;
    top: -12px;
    font-size: 1rem;

    @media (min-width: 1201px) {
      top: -15px;
    }
  }
}

.input-text,
select {
  height: 40px;
  line-height: 40px;

  @media (min-width: 1201px) {
    height: 50px;
    line-height: 50px;
  }
}

.input-textarea {
  padding: 15px 26px;
  max-width: 100%;
  height: 147px;
  line-height: 1.2;
}

input[type="search"] {
  box-sizing: border-box;
}

/* checkbox, radio */
.input-checkbox,
.input-radio {
  position: relative;

  input {
    position: absolute;
    top: 0;
    visibility: hidden;
  }

  &__text {
    position: relative;
    display: block;
    margin: 0;
    padding-left: 36px;
    font-size: 1.6rem;
    line-height: 1.2;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0.6em;
      border-radius: 5px;
    }

    &::before {
      left: 0;
      z-index: 1;
      margin-top: -10px;
      border: 1px solid $color-white;
      width: 18px;
      height: 18px;
      background-color: $color-white;
    }

    &::after {
      left: 1px;
      z-index: 2;
      opacity: 0;
      margin-top: -9px;
      width: 16px;
      height: 16px;
      transition: opacity 0.3s ease;
    }
  }

  input:checked + &__text {
    &::after {
      opacity: 1;
      background-color: $color-guardsman-red;
    }
  }
}

.input-radio {
  &__text {
    &::before,
    &::after {
      border-radius: 50%;
    }
  }
}

/* buttons */
.btn {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  margin: 0;
  border-radius: 5px;
  border: 0 none;
  padding: 0 10px;
  min-width: 95px;
  max-width: 100%;
  height: 26px;
  font-family: $btn-font-family;
  font-size: 1.2rem;
  line-height: 26px;
  text-align: center;
  text-decoration: none;
  color: $color-alto;
  background: $btn-background;
  transition: background 0.3s ease;

  &_darkred {
    background: $color-falu-red;
  }

  &_grey {
    background: $color-emperor;
  }

  &_medium {
    width: 138px;
    height: 36px;
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 36px;
  }

  &_big {
    border-radius: 10px;
    padding: 0 15px;
    width: 186px;
    height: 40px;
    font-size: 1.4rem;
    line-height: 40px;
    letter-spacing: -0.28px;

    @media (min-width: 1201px) {
      height: 50px;
      line-height: 50px;
    }
  }

  &:hover,
  &:active {
    background: $btn-background-hover;
    color: #fff;
  }
}

.fp-tableCell {
  vertical-align: top;
}

.swiper-wrapper {
  box-sizing: border-box;
}