.cart {
  width: 900px;
  max-width: 100%;

  &__table {
    width: 100%;
    font-size: 1.4rem;
    line-height: 1.8;

    td,
    th {
      vertical-align: middle;
    }

    thead th,
    thead td {
      padding-bottom: 25px;
    }

    tfoot th,
    tfoot td {
      padding-top: 7px;
      padding-bottom: 7px;
    }
  }

  &__name {
    text-transform: uppercase;

    a {
      @include link-border($color-white);
    }
  }

  &__delete {
    text-decoration: none;
    white-space: nowrap;
    color: $color-dove-gray;

    &-link,
    &-btn {
      display: inline-block;
      vertical-align: middle;
    }

    &-btn {
      margin-left: 10px;
      width: 13px;
      height: 13px;
      fill: #ed0000;
    }
  }

  &__img {
    text-align: center;

    a {
      display: block;
    }

    img {
      max-width: 100px;
      width: auto;
      max-height: 100px;
      height: auto;
    }
  }

  &__count {
    white-space: nowrap;
  }

  &__quantity,
  &__minus,
  &__plus {
    display: inline-block;
    vertical-align: middle;
    width: 20px;
    height: 30px;
    line-height: 30px;
  }

  &__quantity {
    border: 0 none;
    padding: 0;
    text-align: center;
    color: $color-white;
    background: none;
  }

  &__minus,
  &__plus {
    border: 0 none;
    padding: 0;
    color: $color-dove-gray;
    background: none;
    transition: color 0.3s ease;

    &:active,
    &:hover {
      color: $color-white;
    }
  }

  &__price {
    font-weight: 500;
    white-space: nowrap;
  }

  tfoot {
    font-size: 1.4rem;

    tr:first-child td,
    tr:first-child th {
      padding-top: 50px;
    }
  }

  &__total {
    display: inline-block;
    vertical-align: middle;
    font-weight: 500;
    font-size: 2.1rem;
    line-height: 1;

    &_all {
      position: relative;
      color: rgba($color-white, 0.5);
      
      &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0.5em;
        width: 100%;
        height: 2px;
        background: $color-white;
      }
    }
  }

  &__promo {
    width: 480px;
    max-width: 100%;

    @media (min-width: 481px) {
      padding-right: 15px;
    }

    @media (max-width: 768px) {
      margin-bottom: 20px;
    }
  }

  &__submit {
    @media (min-width: 481px) {
      padding: 0 10px;
    }

    .order-form & {
      padding: 0;
    }

    .btn {
      font-weight: 500;
      width: 335px;
      max-width: 100%;

      .order-form & {
        width: 275px;
      }
    }
  }

  &__btns {
    margin: 25px 0;

    @media (min-width: 481px) {
      display: flex;
      flex-flow: row wrap;
      justify-content: space-between;
      align-items: center;
    }

    @media (min-width: 769px) {
      flex-wrap: nowrap;
    }

    @media (min-width: 769px) and (max-width: 1024px) {
      margin: 35px 0;
    }

    @media (min-width: 1025px) {
      margin: 55px 0;
    }
  }

  &__btn {
    &_reload {
      border-radius: 10px;
      width: 103px;
      background-color: #2f2f2f;
    }
  }

  @media (max-width: 768px) {
    thead {
      display: none;
    }

    &,
    tbody,
    tfoot,
    tr {
      display: block;
    }

    .cart__price {
      display: none;
    }

    tbody tr {
      position: relative;
      border-bottom: 1px solid $color-silver;
      padding: 25px 0 15px;

      &:first-child {
        border-top: 1px solid $color-silver;
      }
    }

    .cart__delete-td,
    .cart__img,
    .cart__name,
    .cart__count,
    .cart__price_sum {
      display: inline-block;
      vertical-align: middle;
      padding: 0 10px 0 0;
    }

    .cart__delete-td {
      position: absolute;
      right: 0;
      top: 0;
      font-size: 1.2rem;
    }

    .cart__count {
      &::after {
        content: attr(data-name-after);
        vertical-align: middle;
        margin-left: 3px;
      }
    }

    .cart__price_sum {
      line-height: 30px;

      &::before {
        content: attr(data-name-before);
        font-weight: 400;
        margin-right: 5px;
      }
    }

    tfoot {
      position: relative;
      border-bottom: 1px solid $color-silver;
      padding-bottom: 65px;

      td,
      th {
        display: inline-block;
        vertical-align: middle;
      }

      tr:first-child td,
      tr:first-child th {
        padding-top: 20px;
      }

      .cart__btn_reload {
        position: absolute;
        bottom: 15px;
        right: 0;
      }
    }
  }

  @media (max-width: 360px) {
    .cart__img,
    .cart__name {
      vertical-align: top;
      padding-bottom: 10px;
    }

    .cart__img {
      width: 25%;

      img {
        max-width: 100%;
      }
    }

    .cart__name {
      width: 70%;
    }
  }
}