$duration: 0.2s;
$delay: 0.05s;

.hamburger {
  position: relative;
  width: 74px;
  height: 100%;
  text-align: center;
  background: $color-mine-shaft;
  cursor: pointer;

  @media (min-width: 481px) and (max-width: 768px) {
    width: 100px;
  }

  @media (min-width: 769px) and (max-width: 1024px) {
    width: 120px;
  }

  @media (min-width: 1025px) {
    width: 140px;
  }

  .is-fixed & {
    width: 74px;
  }

  &__inner {
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -12px 0 0 -12px;
    overflow: hidden;

    @media (min-width: 481px) {
      margin: -20px 0 0 -20px;
    }

    .is-fixed & {
      margin: -12px 0 0 -12px;
    }

    &::before,
    &::after {
      content: "";
      display: block;
      width: 24px;
      height: 6px;
      background: #747474;

      @media (min-width: 481px) {
        width: 40px;
        height: 9px;
      }

      .is-fixed & {
        width: 24px;
        height: 6px;
      }
    }

    span {
      display: block;
      width: 24px;
      height: 6px;
      margin: 3px 0;
      background: #747474;
      transform-origin: 50% 50%;
      transition: background $duration ease-out;

      @media (min-width: 481px) {
        margin: 6px 0;
        width: 40px;
        height: 9px;
      }

      .is-fixed & {
        width: 24px;
        height: 6px;
        margin: 3px 0;
      }

      &:last-child {
        opacity: 0;
        transform: rotate(-45deg);
        position: absolute;
        top: 6px;

        @media (min-width: 481px) {
          top: 10px;
        }

        .is-fixed & {
          top: 6px;
        }
      }
    }

    .is-active & {
      &::before {
        animation: slideLeft $duration ease-out $delay forwards;
      }

      &::after {
        animation: slideRight $duration ease-out $delay forwards
      }

      span {
        animation: rotate45 $duration ease-out forwards;

        &:last-child {
          animation: rotate135 $duration ease-out $duration forwards;
        }
      }
    }

    .is-active:not(.is-back) & span {
      background: $color-red;
    }

    .is-back & {
      &::before {
        transform: translateX(-120%);
        animation: slideBack $duration ease-out $delay forwards;
      }

      &::after {
        transform: translateX(120%);
        animation: slideBack $duration ease-out $delay forwards;
      }

      span {
        transform: rotate(-45deg);
        animation: rotateBack $duration ease-out forwards;

        &:last-child {
          transform: rotate(-135deg);
          animation: rotateBack $duration ease-out $duration forwards;
        }
      }
    }
  }
}

@keyframes rotate135 {
  from {
    opacity: 1;
    transform: rotate(-45deg);
  }
  to {
    opacity: 1;
    transform: rotate(-135deg);
  }
}

@keyframes rotate45 {
  to {
    transform: rotate(-45deg);
  }
}

@keyframes rotateBack {
  to {
    transform: rotate(0deg);
  }
}

@keyframes slideRight {
  to {
    transform: translateX(122%);
  }
}

@keyframes slideLeft {
  to {
    transform: translateX(-122%);
  }
}

@keyframes slideBack {
  to {
    transform: translateX(0)
  }
}