.order-form {
  max-width: 810px;

  .input-text,
  select,
  .input-textarea,
  .input-placeholder {
    width: 100%;
  }

  .row {
    margin-left: -8px;
    margin-right: -8px;
  }

  [class*="col"] {
    padding-left: 8px;
    padding-right: 8px;
  }

  .col-lg-1 .input-text,
  .col-lg-1 select,
  .col-lg-1 .input-textarea,
  .col-lg-1 .input-placeholder__text {
    padding-left: 14px;
    padding-right: 14px;
  }

  .input {
    margin-bottom: 7px;
  }

  &__text {
    margin: 25px 0;
    font-size: 1.4rem;
    letter-spacing: -0.28px;
    line-height: 1.5;
    color: $color-white;

    @media (min-width: 769px) and (max-width: 1024px) {
      margin: 30px 0 40px;
    }

    @media (min-width: 1025px) {
      margin: 40px 0 70px;
      line-height: 2.2;
    }

    a {
      @include link-border($color-white);
      font-weight: 500;
    }
  }

  &__inputs {
    margin-bottom: 25px;

    @media (min-width: 769px) and (max-width: 1024px) {
      margin-bottom: 40px;
    }

    @media (min-width: 1025px) {
      margin-bottom: 70px;
    }
  }
}