.page404 {
  display: table;
  width: 100%;

  &__column {
    display: table-header-group;

    &_img {
      position: relative;
      z-index: 2;

      @media (orientation: landscape) and (max-width: 670px) {
        img {
          max-width: 200px;
        }
      }

      @media (orientation: portrait), (max-width: 768px) {
        display: table-footer-group;
      }

      @media (min-width: 1441px) {
        padding-left: 25px;
        width: 51%;
      }
    }

    @media (orientation: landscape), (min-width: 769px) {
      display: table-cell;
      vertical-align: bottom;
    }
  }

  &__number {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 100px;
    letter-spacing: -6px;
    line-height: 0.85;
    color: $color-guardsman-red;

    @media (min-width: 769px) {
      letter-spacing: -8.06px;
    }

    @media (min-width: 769px) and (max-width: 1024px) {
      font-size: 200px;
    }

    @media (min-width: 1025px) and (max-width: 1440px) {
      font-size: 300px;
    }

    @media (min-width: 1441px) {
      margin-right: -55px;
      font-size: 403px;
      line-height: 0.75;
    }
  }

  &__content {
    padding-bottom: 60px;
    padding-top: 40px;
    font-size: 1.6rem;
    letter-spacing: -0.32px;
    text-align: right;
    color: $color-white;

    @media (max-width: 768px) {
      padding-bottom: 20px;
      padding-top: 20px;
    }

    @media (min-width: 769px) {
      padding-top: 0;
    }

    @media (min-width: 1441px) {
      margin-left: -220px;
      padding-right: 213px;
      padding-bottom: 120px;
    }

    .btn_big {
      height: 40px;
      font-size: 1.6rem;
      line-height: 40px;
    }
  }

  &__text {
    margin-bottom: 15px;
    line-height: 1.5;
    text-transform: uppercase;

    @media (min-width: 671px) {
      margin-bottom: 25px;
      line-height: 2;
    }
  }

  &__text-small {
    opacity: 0.7;
    margin-bottom: 25px;
    line-height: 1.2;

    @media (min-width: 671px) {
      line-height: 1.5625;
    }

    @media (min-width: 1025px) and (max-width: 1200px) {
      margin-bottom: 40px;
    }

    @media (min-width: 1201px) {
      margin-bottom: 65px;
    }
  }
}