.catalog {
  height: 100%;
  letter-spacing: 0;

  &__header {
    padding-top: 20px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;

    .home__inner & {
      padding-top: 55px;
    }

    @media (min-width: 769px) {
      flex-wrap: nowrap;
    }

    @media (min-width: 1025px) {
      padding: 47px 0;
    }

    .home__inner & {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  &__back {
    @media (min-width: 1025px) {
      margin-top: 7px;
    }

    @media (min-width: 1781px) {
      margin-left: -30px;
    }
  }

  &__menu {
    font-size: 1.4rem;
    line-height: 1.2;

    @media (max-width: 769px) {
      margin-top: 35px;
      width: 100%;
      order: 3;
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    li {
      display: inline-block;
      vertical-align: top;
      margin: 0;
      padding: 5px 10px 5px 0;

      @media (max-width: 480px ) {
        &:nth-child(2n+1) {
          width: 46%;
        }

        &:nth-child(2n+2) {
          margin-right: -15px;
          width: 55%;
        }
      }

      @media (min-width: 481px) and (max-width: 768px) {
        padding-right: 15px;
      }

      @media (min-width: 769px) and (max-width: 1024px) {
        padding-right: 25px;
      }

      @media (min-width: 1025px) {
        padding-right: 30px;
      }

      a {
        opacity: 0.5;
        text-decoration: none;
        transition: opacity 0.2s ease;
      }

      &.is-active a,
      & a:hover {
        opacity: 1;

        @media (max-width: 480px ) {
          text-decoration: underline;
        }
      }

      @media (min-width: 769px) {
        &.is-active {
          position: relative;

          &::before {
            content: "";
            display: block;
            position: absolute;
            left: 50%;
            top: -55px;
            z-index: 1;
            border-radius: 50%;
            margin-left: -50%;
            margin-top: -55px;
            width: 100%;
            height: 35px;
            box-shadow: 0 40px 40px 10px rgba($color-white,0.45);
          }
        }
      }
    }
  }

  &__shop-link,
  .cart-link {
    position: relative;

    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: -110px;
      z-index: 1;
      border-radius: 50%;
      margin-top: -55px;
      width: 300px;
      height: 50px;
      box-shadow: 0 90px 40px 10px rgba($color-red,0.45);
      transition: margin 0.3s ease;

      @media (min-width: 769px) {
        top: -110px;
        margin-left: -100%;
        width: 200%;
        height: 35px;
      }
    }

    &:hover::before {
      margin-top: -35px;
    }
  }

  &__shop-link {
    font-size: 1.4rem;

    @media (max-width: 768px) {
      &::before {
        right: auto;
        left: 0;
        margin-left: -200px;
      }
    }

    a {
      text-decoration: none;
    }
  }

  .cart-link {
    @media (max-width: 768px) {
      &::before {
        right: 0;
        left: auto;
        margin-right: -200px;
      }
    }
  }

  &__items {
    box-sizing: border-box;
    position: relative;
    margin: 0 -30px;
    padding: 0 30px;

    @media (min-width: 1401px) {
      padding: 0 40px;
      margin: 0 -40px;
    }

    &-inner {
      &:not(.js-catalog-slider) {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        align-items: flex-start;
        margin: 0 -25px;
        padding: 25px 0 0;

        @media (min-width: 1781px) {
          margin: 0 -95px;
        }

        &::after {
          content: "";
          flex: 1 1 auto;
        }
      }

      &.js-catalog-slider {
        padding: 110px 0;

        @media (min-width: 1401px) {
          padding: 40px 0;
        }
      }
    }

    .slick-arrow {
      top: 170px;
      bottom: auto;
      margin: 0;

      @media (min-width: 1025px) {
        top: 50%;
        margin-top: -15px;
      }
    }

    .slick-prev {
      left: 15px;
    }

    .slick-next {
      right: 15px;
    }

    .slick-slide > div,
    .slick-track > div {
      width: 100%;
    }
  }

  &__item {
    flex-shrink: 0;
    margin-bottom: 60px;
    padding: 0 20px;
    width: 100%;

    .home__inner & {
      margin-bottom: 30px;
    }

    @media (min-width: 640px) and (max-width: 768px) {
      width: 50%;
    }

    @media (min-width: 769px) and (max-width: 1300px) {
      width: 33.33%;
    }

    @media (min-width: 1301px) {
      width: 25%;
    }
  }
}