.catalog-item {
  position: relative;
  letter-spacing: 0;
  text-align: center;
  color: $color-white;

  &__img {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    width: 200px;
    max-width: 100%;
    height: 200px;

    &::before {
      content: "";
      opacity: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 1;
      margin: -70% 0 0 -60%;
      width: 120%;
      height: 140%;
      background: radial-gradient(farthest-side, rgba(255, 255, 255, 0.4) 0%, rgba(255,255,255,0) 100%);
      transition: opacity 0.5s ease;
    }
    
    img {
      position: relative;
      z-index: 2;
      opacity: 0.8;
      transition: opacity 0.5s ease;
    }
  }

  &:hover &__img {
    img,
    &::before {
      opacity: 1;
    }
  }

  &__content {
    overflow: hidden;
    position: relative;
    opacity: 0.5;
    margin: 0 auto;
    padding-bottom: 35px;
    max-width: 250px;
    transition: all 0.4s ease-in-out;
  }

  &__img + &__content {
    margin-top: 55px;

    @media (min-width: 481px) {
      margin-top: 30px;
    }
  }

  &__name {
    display: block;
    margin: 0 0 0.65em;
    padding: 0;
    font-weight: 500;
    font-size: 1.7rem;
    line-height: 1.33;
    letter-spacing: 0;
    text-transform: uppercase;

    .home__inner & {
      font-size: 2.1rem;
    }

    a {
      text-decoration: none;
    }

    a,
    span {
      border-bottom: 1px solid transparent;
      transition: border 0.2s ease;
    }

    &:hover span {
      border-bottom-color: rgba($color-white, 0.75);
    }
  }

  &__descr {
    overflow: hidden;
    max-height: 3.2em;
    font-size: 1rem;
    line-height: 1.6;

    .home__inner & {
      max-height: 4.8em;
    }
  }

  &__prices {
    margin: 22px -10px 5px;

    .home__inner & {
      margin: 16px -10px;
    }
  }

  &__price {
    display: inline-block;
    vertical-align: middle;
    margin: 3px 10px;

    &.is-old {
      text-decoration: line-through;
    }
  }

  &__btns {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    white-space: nowrap;
  }

  &__btn {
    margin: 6px;
    height: 23px;
    font-size: 0.9rem;
    line-height: 23px;
    transform: translateY(150%);
    transition: transform 0.4s ease-in-out;

    & + & {
      transition-delay: 0.2s;
    }

    .home__inner & {
      height: 26px;
      font-size: 1.2rem;
      line-height: 26px;
    }
  }

  &.is-open &__content,
  &:hover &__content {
    opacity: 1;
  }

  &.is-open &__btn,
  &:hover &__btn {
    transform: none;
  }

  &.is-open &__btns,
  &:hover &__btns {
    opacity: 1;
  }
}