.news-page {
  position: relative;
  padding-top: 40px;

  @media (min-width: 769px) and (max-width: 1024px) {
    padding-top: 20vh;
  }

  @media (min-width: 1025px) and (max-width: 1200px) {
    padding-top: 30vh;
  }

  @media (min-width: 1201px) {
    padding-top: 43vh;
  }

  &::before {
    content: "";
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $color-black;
  }

  &__bg {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;
    height: 110vh;
    background-color: $color-black;
    background-position: center top;
    background-repeat: no-repeat;
    background-size: 100% auto;
    @include blur;
    transition: transform 0.01s;
  }

  &__back {
    position: relative;
    z-index: 2;
    max-width: 95%;
    margin: 0 auto 40px;

    @media (min-width: 769px) {
      position: absolute;
      left: 20px;
      top: 80px;
      margin: 0;
    }

    @media (min-width: 1781px) {
      left: 50%;
      margin-left: -842px;
    }
  }

  &__content {
    position: relative;
    z-index: 2;
    overflow: hidden;
    border: 1px solid $color-dove-gray;
    border-radius: 10px;
    padding: 10px 15px;
    margin: 0 auto;
    max-width: 95%;
    font-size: 1.4rem;
    line-height: 1.5;
    color: $color-emperor;
    background: $color-white;

    @media (min-width: 769px) {
      border-radius: 20px;
    }

    @media (min-width: 769px) and (max-width: 1024px) {
      padding: 30px 60px;
    }

    @media (min-width: 1025px) and (max-width: 1200px) {
      padding: 70px 100px;
    }

    @media (min-width: 1201px) {
      padding: 100px 150px;
      width: 1120px;
    }

    p {
      margin-bottom: 1.45em;
    }
  }

  &__title {
    margin-bottom: 0.75em;
    font-weight: 500;
    letter-spacing: 0;
    color: inherit;

    @media (min-width: 1025px) {
      font-size: 3.2rem;
    }
  }

  &__date {
    opacity: 0.5;
    font-size: 1.6rem;
    line-height: 1;
  }

  &__video {
    display: block;
    position: relative;
    margin: 35px 0;
    border-radius: 10px;
    padding-top: 45%;
    width: 100%;
    background-color: #d8d8d8;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      pointer-events: none;
    }

    &::before {
      margin: -49px 0 0 -49px;
      border-radius: 50%;
      width: 98px;
      height: 98px;
      background: #919191;
    }

    &::after {
      margin: -15px 0 0 -11px;
      border-width: 15px 26px;
      border-style: solid;
      border-color: transparent transparent transparent $color-white;
    }

    @media (min-width: 769px) {
      border-radius: 20px;
      margin: 45px 0 50px;
    }
  }

  &__descr {
    margin-bottom: 65px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__recommended {
    margin: 0 -15px -10px -15px;
    padding: 10px 15px;
    background: $color-mine-shaft;

    @media (min-width: 769px) and (max-width: 1024px) {
      margin: 0 -60px -30px -60px;
      padding: 30px 60px;
    }

    @media (min-width: 1025px) and (max-width: 1200px) {
      margin: 0 -100px -70px -100px;
      padding: 45px 100px;
    }

    @media (min-width: 1201px) {
      margin: 0 -150px -100px -150px;
      padding: 55px 145px 60px;
    }
  }

  &__recommended-title {
    margin: 0 0 1em;
    opacity: 0.6;
    font-weight: 500;
    font-size: 2.1rem;
    line-height: 1.2;
    text-align: center;

    @media (min-width: 1025px) {
      margin-bottom: 45px;
    }
  }
}