.social-links {
  text-align: center;

  &__item {
    display: inline-block;
    vertical-align: middle;
    margin: 5px 15px;
  }

  a {
    opacity: 0.5;
    text-decoration: none;
    transition: opacity 0.2s ease;

    &:hover {
      opacity: 1;
    }
  }

  svg {
    display: inline-block;
    vertical-align: middle;
    width: 22px;
    height: 18px;
  }

  img {
    width: auto;
    max-width: 22px;
    height: auto;
    max-height: 18px;
  }
}