.news-item {
  overflow: hidden;
  position: relative;
  padding-top: 84.68%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @media (max-width: 640px) and (max-height: 767px) {
    padding-top: 130%;
  }

  .home__section & {
    @media (min-height: 768px) {
      padding-top: 0;
      height: 100%;
    }
  }

  &__inner {
    position: absolute;
    left: 35px;
    right: 35px;
    top: 55px;
    bottom: 30px;

    @media (min-width: 1025px) and (max-width: 1400px) {
      left: 35px;
      right: 35px;
      top: 35px;
      bottom: 35px;
    }

    @media (min-width: 1401px) {
      left: 60px;
      right: 60px;
      top: 57px;
      bottom: 57px;
    }
  }

  &__height {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-flow: column nowrap;
    position: absolute;
    left: 0;
    overflow: hidden;
    padding-bottom: 55px;
    width: 350px;
    max-width: 100%;
    height: 7em;
    transition: all 0.3s;

    @media (min-width: 769px) {
      bottom: 0;
      height: 6.3em;
    }
  }

  &__name {
    display: block;
    position: relative;
    opacity: 0.6;
    margin: 0 0 0.55em;
    padding: 0;
    font-weight: 400;
    font-size: 2.1rem;
    letter-spacing: -1.4px;
    text-decoration: none;
    color: $color-alabaster;

    @media (min-width: 361px) and (max-width: 768px) {
      padding-bottom: 15px;
      height: 4em;

      &::after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 2;
        width: 81px;
        height: 3px;
        background: $global-accent-color;
      }
    }

    a {
      text-decoration: none;
    }

    a,
    span {
      display: inline-block;
      overflow: hidden;
      height: 3.45em;
      line-height: 1.15;
    }

    @media (min-width: 769px) {
      opacity: 1;
    }
  }

  &__content {
    overflow: hidden;
    max-height: 100%;
    font-size: 1.4rem;
  }

  &__date,
  &__descr,
  &__more {
    transform: translate3d(0, 500px, 0);
    transition: transform 0.3s ease;
  }

  &__date {
    position: relative;
    margin: 0 0 25px;
    padding: 0 0 23px;
    font-style: italic;
    font-weight: 300;
    font-size: 1.6rem;
    line-height: 1.625;
    opacity: 0.7;
    color: $color-alabaster;
    transition-delay: 0.1s;

    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 2;
      width: 81px;
      height: 3px;
      background: $global-accent-color;
    }
  }

  &__descr {
    overflow: hidden;
    max-height: 5.8em;
    font-weight: 300;
    font-style: italic;
    font-size: 1.5rem;
    line-height: 1.45;
    color: $color-alabaster;
    transition-delay: 0.2s;

    @media (min-width: 1025px) {
      max-height: 7.25em;
    }

    @media (max-width: 1024px) {
      .content-wrap > :not(.home) .news & {
        display: none;
      }
    }
  }

  &__more {
    position: absolute;
    left: 0;
    bottom: 0;
    transition-delay: 0.3s;
  }

  &__btn {
    display: inline-block;
    vertical-align: middle;
    padding: 0 10px;
    overflow: hidden;
    max-width: 100%;
    height: 40px;
    font-weight: 500;
    font-size: 2.1rem;
    line-height: 40px;
    text-align: center;
    text-decoration: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: $color-white;
    background: $btn-background;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.2;
    width: 100%;
    height: 100%;
    background: rgba($color-cod-gray, 0.8);
    transition: opacity 1s ease-in-out;
  }

  &.is-open,
  &:hover {
    &::before {
      opacity: 1;
      transform: none;
    }
  }

  &.is-open &__name,
  &:hover &__name {
    opacity: 1;
    padding-bottom: 0;
    height: 3.45em;

    &::after {
      display: none;
    }
  }

  &.is-open &__height,
  &:hover &__height {
    height: 100%;
  }

  &.is-open &__date,
  &.is-open &__descr,
  &.is-open &__more,
  &:hover &__date,
  &:hover &__descr,
  &:hover &__more {
    transform: translate3d(0, 0, 0);
  }
}
