// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS
$color-falu-red: #791717;
$color-tamarillo: #861313;
$color-guardsman-red: #ce0404;
$color-red: #ff0000;
$color-valencia: #D34A4A;

$color-gold: #f8d400;

$color-black: #000000;
$color-nero: #010100;
$color-cod-gray: #1a1a1a;
$color-mine-shaft: #383838;
$color-tundora: #434343;
$color-emperor: #505050;
$color-dove-gray: #707070;
$color-hurricane: #908787;
$color-silver-chalice: #aaaaaa;
$color-silver: #c1c1c1;
$color-alto: #d6d6d6;
$color-gallery: #efefef;
$color-concrete: #f2f2f2;
$color-alabaster: #f7f7f7;
$color-white: #ffffff;

// FONTS
$font-family-rubik: Rubik, Arial, sans-serif;

// typography
$body-background: $color-cod-gray;
$body-font-color: $color-white;
$body-font-family: $font-family-rubik;
$global-font-size: 1.2rem;
$global-weight-normal: 400;
$global-line-height: 1.33;

$global-accent-color: $color-guardsman-red;

$headers-font-color: $color-white;
$headers-font-family: $font-family-rubik;
$headers-font-weight: 500;

$blockquote-border-color: $color-white;
$blockquote-border-width: 3px;

// FORMS
$placeholder-color: rgba($color-white, 0.5);
$input-color: $color-white;
$input-border-color: $color-black;
$input-border-color-focus: $color-black;
$input-background: #121212;

// BTN
$btn-background: $color-guardsman-red;
$btn-background-hover: $color-black;
$btn-color: $color-white;
$btn-font-family: $font-family-rubik;

// HR
$hr-bg-color: rgba($color-dove-gray, 0.3);

// MEDIA
$media-mobile-width-min: 769px;
$media-mobile-width-max: 768px;