.ajax-loader {
  display: none;
  vertical-align: middle;
  user-select: none;

  .is-loader & {
    display: inline-block;
  }

  div {
    margin: 0 0 5px;
    font-size: 1rem;
    line-height: 1;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    color: $color-alabaster;
  }

  &__line {
    display: inline-block;
    vertical-align: middle;
    margin: 0 1px;
    width: 1px;
    height: 10px;
    background: #fff;
    animation: ajax-loader-opacity 1s ease-in-out infinite;

    &_1 {
      animation-delay: 0.8s;
    }

    &_2 {
      animation-delay: 0.6s;
    }

    &_3 {
      animation-delay: 0.4s;
    }

    &_4 {
      animation-delay: 0.2s;
    }

    &_6 {
      animation-delay: 0.2s;
    }

    &_7 {
      animation-delay: 0.4s;
    }

    &_8 {
      animation-delay: 0.6s;
    }

    &_9 {
      animation-delay: 0.8s;
    }
  }
}

@keyframes ajax-loader-opacity {
  0% {
    opacity: 1;
    height: 13px;
  }

  50% {
    opacity: 0;
    height: 10px;
  }

  100% {
    opacity: 1;
    height: 13px;
  }
}