.link-back {
  display: inline-block;
  vertical-align: middle;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1;
  letter-spacing: -0.32px;
  white-space: nowrap;
  transform: none;
  transition: transform 0.3s ease;

  @media (min-width: 481px) {
    font-size: 1.6rem;
  }

  &__icon,
  &__text {
    display: inline-block;
    vertical-align: middle;
  }

  &__icon {
    margin-right: 15px;
  }

  &__text {
    white-space: normal;

    @media (max-width: 480px) {
      max-width: 100px;
    }
  }

  svg {
    width: 15px;
    height: 21px;
    stroke: rgba($color-white, 0.5);
  }

  &:hover {
    transform: translateX(-10px);
  }
}