.home-slider {
  position: relative;
  width: 100%;
  //background: $color-alabaster url("../images/pic/home/bg_home-slider.jpg") no-repeat left top;
  background-size: cover;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    background: linear-gradient(to top, #fdfdfd 0%, #fefefe 37%, rgba(255, 255, 255, 0.36) 100%);
  }

  .swiper-container {
    width: 100%;
    flex-flow: row nowrap;
  }

  .swiper-wrapper {
    position: relative;
    z-index: 3;
  }

  @media (min-width: 769px) {
    background-position: center top;
  }

  .start-page__slider & {
    height: 100%;
  }

  .slick-list,
  .slick-track {
    height: 100%;
  }

  &.swiper-container,
  .slick-list {
    overflow: inherit;
  }

  &__item,
  &__content {
    height: 100%;
  }

  &__item {
    flex: 0 0 100%;
    width: 100%;
    background-repeat: no-repeat;

    &_video {
      text-align: center;
      background: #000;
    }

    @media (max-width: 320px) {
      background: none;
    }

    @media (max-width: 768px) {
      background-position: center bottom !important;
    }

    @media (min-width: 769px) {
      background-repeat: no-repeat;
      background-position: center;
    }

    @media (max-width: 1600px) {
      background-size: 120% auto;
    }
  }

  &__content {
    visibility: hidden;
    opacity: 0;
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    height: 100%;

    &.is-visible {
      visibility: visible;
      opacity: 1;
    }

    @media (min-width: 769px) {
      display: table;
      table-layout: fixed;
    }

    @media (min-width: 1681px) {
      width: 1680px;
    }
  }

  &__title {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 4rem;
    line-height: 0.85;
    letter-spacing: -2.4px;
    text-transform: uppercase;
    color: $color-black;

    @media (max-width: 768px) {
      br {
        display: none;
      }
    }

    @media (min-width: 769px) and (max-width: 1200px) {
      font-size: 6rem;
    }

    @media (min-width: 1201px) and (max-width: 1680px) {
      margin-bottom: 30px;
      font-size: 8rem;
      line-height: 0.85;
      letter-spacing: -4.8px;
    }

    @media (min-width: 1681px) {
      margin-bottom: 40px;
      font-size: 12rem;
      line-height: 0.75;
      letter-spacing: -7.2px;
    }

    @media (min-width: 1025px) and (max-height: 768px) {
      font-size: 6rem;
      line-height: 0.85;
      letter-spacing: -2.4px;
    }

    &_small {
      @media (min-width: 1681px) {
        font-size: 8rem;
        line-height: 0.85;
        letter-spacing: -4.8px;
      }
    }
  }

  &__descr,
  &__img {
    @media (max-width: 768px) {
      overflow: hidden;
      width: 100%;
      height: 50%;
    }

    @media (min-width: 769px) {
      display: table-cell;
      vertical-align: middle;
    }
  }

  &__descr {
    padding: 40px 0;
    letter-spacing: 0;

    @media (min-width: 769px) {
      padding: 8.5% 50px 8.5% 0;
      width: 550px;
      max-width: 100%;
    }

    @media (min-width: 769px) and (max-height: 980px) {
      padding: 50px 50px 50px 0;
    }
  }

  &__img {
    text-align: center;
    
    img {
      display: inline-block;
      vertical-align: middle;
      width: auto;
      max-height: 100%;

      @media (max-width: 768px) {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 50%;
        margin: auto;
        max-height: 50%;
      }
    }

    @media (min-width: 769px) {
      width: 70%;
      position: relative;
      text-align: left;
    }

    &_floid {
      @media (min-width: 769px) {
        position: static;
        height: 100%;
        vertical-align: bottom;
      }

      img {
        position: absolute;
        top: auto;
        bottom: 0;
        margin: 0 auto;

        @media (min-width: 769px) {
          left: 400px;
          right: 0;
          max-width: 50%;
          max-height: 100%;
        }
      }
    }

    @media (min-width: 1201px) {
      padding-left: 80px;
    }
  }

  &__text {
    max-width: 410px;
    font-weight: 300;
    font-style: italic;
    font-size: 1.4rem;
    line-height: 1.5;
    color: $color-emperor;

    @media (min-width: 1025px) {
      font-size: 1.5rem;
      line-height: 1.7;
    }
  }
}