/* typography */
html {
	box-sizing: border-box;
	font-size: 10px;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  width: 100%;
  min-width: 320px;
  font-family: $body-font-family;
  font-size: $global-font-size;
  font-weight: $global-weight-normal;
  line-height: $global-line-height;
  letter-spacing: -0.24px;
  color: $body-font-color;
  background: $body-background;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  color: $headers-font-color;
  font-family: $headers-font-family;
  font-weight: $headers-font-weight;
  line-height: 1.3;
}

h1,
.h1 {
  margin: 0 0 1.2em;
  padding: 0;
  font-size: 2.1rem;
  letter-spacing: -0.42px;
  line-height: 1.15;

  svg {
    display: inline;
    vertical-align: top;
    margin: 2px 30px 0 0;
    width: 20px;
    height: 20px;
    transition: margin 0.2s ease;

    @media (min-width: 1781px) {
      margin-left: -50px;
    }
  }

  &_icon {
    font-size: 1.8rem;
    letter-spacing: -0.36px;
  }
}

h2,
.h2 {
  margin: 2em 0 1em;
  padding: 0;
  font-size: 1.8rem;
  letter-spacing: -0.36px;
}

h3,
.h3 {
  margin: 2em 0 1em;
  padding: 0;
  font-size: 1.6rem;
  letter-spacing: -0.32px;
}

h4,
.h4 {
  margin: 2em 0 1em;
  padding: 0;
  font-size: 1.4rem;
  letter-spacing: -0.28px;
  line-height: 1.5;
}

h5,
.h5 {
  margin: 2em 0 1em;
  padding: 0;
  font-size: 1.2rem;
  letter-spacing: -0.24px;
  line-height: 1.5;
}

h6,
.h6 {
  margin: 2em 0 1em;
  padding: 0;
  font-size: 1rem;
  letter-spacing: -0.2px;
  line-height: 1.6;
}

p {
  margin: 0 0 1em;
}

hr {
  margin: 20px 0;
  border: 0 none;
  height: 2px;
  background: $hr-bg-color;

  @media (min-width: 1025px) {
    margin: 50px 0;
  }
}

*:focus,
*:active {
  outline: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
}

a {
  text-decoration: underline;
  color: inherit;

  &:active, 
  &:hover, 
  &:focus {
    outline: none;
    text-decoration: none;
    color: inherit;
  }

  &[href^=mailto] {
    line-height: 1.2;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &[href^=tel] {
    display: inline-block;
    text-decoration: none;
    vertical-align: top;
    white-space: nowrap;
  }
}

blockquote {
  position: relative;
  margin: 1.5em 0;
  padding: 35px;
  border-radius: 30px;
  font-style: italic;
  font-weight: 300;
  font-size: 1.4rem;
  line-height: 1.55;
  color: $color-white;
  background: $color-tamarillo;

  &::before,
  &::after {
    position: absolute;
    font-weight: 400;
    font-size: 129px;
    line-height: 1;
    letter-spacing: -2.58px;
    color: $color-valencia;
  }

  &::before {
    content: "";
    left: 10px;
    top: 0;
    margin-top: -25px;
  }

  &::after {
    content: "";
    right: 40px;
    bottom: 0;
    margin-bottom: -100px;
  }
}

strong,
b {
  font-weight: 500;
}

em,
dfn,
i {
  font-style: italic;
}

ul,
ol {
  padding: 0
}

ul {
  list-style-type: disc;
  list-style-position: outside;
  margin: 0 0 1em 1.2em;
}

ol {
  list-style-type: decimal;
  list-style-position: outside;
  margin: 0 0 1em 1.2em;
}

.content-wrap ul:not([class]),
.content-wrap ol:not([class]) {
  font-size: 1.4rem;
  color: $color-guardsman-red;

  li * {
    color: $color-white;
  }
}

li {
  margin: 0 0 0.25em;
  padding: 0 0 0 0.5em;

  ul,
  ol {
    margin: 0.5em 0 0.5em 1em;
    padding: 0;
  }
}

img {
  display: inline;
  vertical-align: top;
  border: 0;
  max-width: 100%;
  height: auto;
}

iframe {
  border: 0;
  max-width: 100%;
}

.text-light {
  font-weight: 300;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-underline {
  text-decoration: underline;
}

.text-line-through {
  text-decoration: line-through;
}

.text-justify {
  text-align: justify;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.text-red {
  color: $color-guardsman-red;
}

.text-grey {
  color: $color-dove-gray;
}

table {
  border: 0 none;
  border-collapse: collapse;

  p:last-child, ul:last-child, ol:last-child {
    margin-bottom: 0;
  }
}

th {
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 1.3;
  text-align: left;
}

th,
td {
  vertical-align: top;
  border: 0 none;
  padding: 13px 20px;

  &:first-child {
    padding-left: 0;
  }
}