.footer {
  padding: 20px 0;
  text-align: center;

  @media (min-width: 1025px) {
    padding: 25px 0 57px;
  }

  &__row {
    justify-content: space-between;
  }

  &__column {
    .body-bg-floid &_center {
      margin-left: 0;
    }
  }

  &__copyright {
    opacity: 0.8;
    font-size: 1rem;
    line-height: 1.9;
    text-transform: uppercase;

    @media (min-width: 769px) {
      font-size: 1.2rem;
      line-height: 1.2;
    }

    .body-without-copyright & {
      display: none;
    }

    .body-bg-floid & {
      text-align: left;
    }
  }

  &__madeby {
    opacity: 0.5;
    font-size: 1rem;
    letter-spacing: 0;
    line-height: 1.2;
    transition: opacity 0.2s ease;

    &:hover {
      opacity: 1;
    }

    @media (min-width: 769px) {
      font-size: 1.4rem;
      text-align: right;
    }

    a {
      @include link-border($color-white);
    }
  }
}