.cart-link {
  display: block;
  position: relative;
  padding-left: 34px;
  min-height: 20px;
  font-size: 1.4rem;
  color: $color-emperor;
  text-decoration: none !important;

  .catalog & {
    color: #e2e2e2;
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -12px;
    width: 20px;
    height: 16px;
    fill: $color-guardsman-red;

    img,
    svg {
      display: inline-block;
      vertical-align: middle;
      max-width: 100%;
      max-height: 100%;
    }
  }

  &__content {
    display: none;
    line-height: 20px;
    white-space: nowrap;

    .is-big & {
      display: block;
    }
  }

  &__text {
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    transition: border 0.4s;
  }

  &:hover &__text,
  &:active &__text,
  &:focus &__text {
    border-bottom-color: rgba($color-emperor, 0.8);
    text-decoration: none;
  }

  &__count {
    margin-left: 5px;
    color: $color-guardsman-red;

    &::before {
      content: "(";
    }

    &::after {
      content: ")";
    }
  }
}