.product-images {
  display: table;
  table-layout: fixed;
  width: 100%;

  &__small,
  &__big {
    display: table-cell;
    vertical-align: middle;
  }

  &__small {
    position: relative;
    z-index: 3;
    width: 145px;

    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 0;
      z-index: 1;
      border-radius: 50%;
      width: 0;
      height: 100%;
      box-shadow: 0 -50px 80px 10px rgba($color-white, 0.5), 0 50px 80px 10px rgba($color-white, 0.5);
    }

    .slick-list {
      z-index: 2;
    }

    @media (max-width: 640px), (min-width: 821px) and (max-width: 1024px) {
      display: table-footer-group;
      width: 100%;

      &::before {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: 50%;
        z-index: 1;
        border-radius: 50%;
        width: 100%;
        height: 0;
        box-shadow: 0 -50px 80px 10px rgba($color-white, 0.5), 0 50px 80px 10px rgba($color-white, 0.5);
      }
    }
  }

  &__preview {
    display: block;
    position: relative;
    z-index: 2;
    opacity: 0.5;
    margin: 25px 16px;
    max-width: 115px;
    transition: all 0.5s ease;

    &:hover,
    &.is-active {
      opacity: 1;
    }
  }

  &__big {
    text-align: center;

    span {
      display: inline-block;
      vertical-align: middle;
      position: relative;
      max-height: 100%;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 1;
        width: 110%;
        height: 130%;
        background: radial-gradient(farthest-side, rgba(255, 255, 255, 0.4) 0%, rgba(255,255,255,0) 100%);
        transform-origin: 50% 50%;
        transform: translate(-50%, -50%);
        transition: opacity 0.5s ease;
      }
    }

    img {
      position: relative;
      z-index: 2;
      max-height: 100%;
    }
  }
}