.orders {
  margin: 25px 0;
  width: 665px;
  max-width: 100%;
  font-weight: 400;
  font-size: 1.4rem;

  @media (min-width: 769px) and (max-width: 1200px) {
    margin: 45px 0;
  }

  @media (min-width: 1201px) {
    margin: 95px 0 55px;
  }

  table {
    width: 100%;
  }

  tr {
    opacity: 0.7;
    transition: opacity 0.4s;
  }

  thead tr,
  tr:hover,
  &__name {
    opacity: 1;
  }

  th {
    padding: 10px 15px;
    font-weight: 500;
    font-size: 1.6rem;
    text-align: left;

    @media (min-width: 769px) {
      padding: 20px 30px;
    }

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  td {
    padding: 7px 15px;
    text-align: left;

    @media (min-width: 769px) {
      padding: 7px 30px;
    }

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  &__status {
    @media (min-width: 521px) {
      font-size: 1.6rem;
    }

    a {
      text-decoration: none;
      @include link-border($color-white);
    }
  }

  @media (max-width: 520px) {
    &,
    tbody,
    tr {
      display: block;
    }

    thead {
      display: none;
    }

    tr {
      @media (max-width: 520px) {
        padding: 10px 0;

        & + tr {
          border-top: 1px solid #fff;
          border-top: 1px solid rgba(#fff, 0.7);
        }
      }
    }

    td {
      display: inline-block;
      vertical-align: middle;
      padding: 5px;
    }

    &__status,
    &__price {
      width: 48%;
    }

    &__price {
      text-align: right;
    }
  }
}