.tpl-menu {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 101;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  overflow: auto;
  width: 100%;
  height: 100%;
  letter-spacing: 0;
  text-align: center;
  color: $color-white;
  background: rgba($color-black, 0.8);
  transform: translateY(-100%);
  transition: transform 0.5s ease-in-out;

  &.is-open {
    transform: none;
  }

  &__header,
  &__inner {
    width: 100%;
  }

  &__header {
    flex: 0 0 auto;
    background: #000;
  }

  .tpl-header__inner {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    line-height: 1.1;
  }

  &__inner {
    flex: 1 1 auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 35px 20px;
    width: 100%;

    @media (min-width: 1025px) {
      padding: 60px 20px;
    }
  }

  &__menu {
    width: 100%;
  }

  &__footer {
    opacity: 0.8;
    width: 100%;
  }

  &__row {
    & + & {
      margin-top: 70px;
    }
  }

  &__mail {
    font-size: 1.2rem;
    line-height: 1.5;

    @media (min-width: 1025px) {
      font-size: 1.4rem;
    }

    a {
      display: block;
      margin-top: 10px;
      font-weight: 500;
      font-size: 1.4rem;
      text-decoration: none;

      @media (min-width: 1025px) {
        font-size: 1.8rem;
      }

      span {
        border-bottom: 1px solid transparent;
        transition: border 0.2s;

        &:hover {
          text-decoration: none;
          border-bottom-color: rgba($color-white, 0.8);
        }
      }
    }
  }

  &__copyright {
    font-size: 1rem;
    line-height: 1.9;
    text-transform: uppercase;

    @media (min-width: 769px) {
      font-size: 1.2rem;
      line-height: 1.2;
    }
  }

  &__madeby {
    opacity: 0.5;
    font-size: 1rem;
    letter-spacing: 0;
    line-height: 1.2;
    transition: opacity 0.2s ease;

    &:hover {
      opacity: 1;
    }

    @media (min-width: 769px) {
      font-size: 1.2rem;
      text-align: right;
    }

    a {
      @include link-border($color-white);
    }
  }
}