.lang-bar {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  opacity: 0.7;
  border: 1px solid $color-guardsman-red;
  border-radius: 2px;
  font-size: 0;
  text-transform: uppercase;
  white-space: nowrap;

  &__item {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    width: 34px;
    height: 27px;
    font-size: 0.9rem;
    line-height: 27px;
    text-decoration: none;
    text-align: center;
    color: $color-guardsman-red;
    transition: all 0.3s;

    &.is-active {
      color: $color-white;
      background: $color-guardsman-red;
    }
  }

  .tpl-header & {
    opacity: 1;
    border-color: $color-dove-gray;

    &__item {
      color: $color-dove-gray;

      &.is-active {
        color: $color-white;
        background: $color-dove-gray;
      }
    }
  }
}