.pk {
  &__form {
    margin: 25px 0;

    @media (min-width: 1025px) and (max-width: 1440px) {
      margin: 50px 0;
    }

    @media (min-width: 1441px) {
      margin: 80px 0;
    }

    & + & {
      margin-top: 50px;

      @media (min-width: 1025px) and (max-width: 1440px) {
        margin-top: 70px;
      }

      @media (min-width: 1441px) {
        margin-top: 110px;
      }
    }

    &:last-child {
      margin-bottom: 35px;
    }

    .row {
      margin-left: -8px;
      margin-right: -8px;
    }

    [class*="col"] {
      padding-left: 5px;
      padding-right: 5px;
    }

    .input {
      margin-bottom: 12px;
    }

    .input-text,
    select,
    .input-textarea,
    .input-placeholder__text {
      padding-left: 14px;
      padding-right: 14px;
      border-radius: 7px;
    }

    .input-text,
    select,
    .input-placeholder__text {
      height: 38px;
      line-height: 38px;
    }

    .is-value .input-placeholder__text,
    .input-placeholder__input:focus + .input-placeholder__text {
      top: -10px;
    }
  }

  &__inputs {
    max-width: 370px;
  }

  &__news {
    margin-top: 25px;

    @media (min-width: 1025px) {
      margin-top: 50px;
    }
  }

  .input_checkbox {
    margin: 0;
    transition: margin 0.2s ease;

    @media (min-width: 1781px) {
      margin-left: -34px;
    }
  }
}