.news-small-item {
  display: block;
  text-decoration: none;

  @media (min-width: 641px) {
    text-align: center;
  }

  &__img {
    position: relative;
    border-radius: 10px;
    padding-top: 57.7%;
    width: 100%;
    background-color: #e8e4e4;
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;

    @media (max-width: 640px) {
      float: left;
      padding-top: 27%;
      width: 45%;
      margin-right: 20px;
    }
  }

  &__img + &__name {
    @media (min-width: 641px) and (max-width: 1025px) {
      margin-top: 15px;
    }

    @media (min-width: 1025px) {
      margin-top: 45px;
    }
  }

  &__name {
    opacity: 0.6;
    letter-spacing: -0.32px;
    color: $color-white;
    transition: opacity 0.3s ease;

    @media (max-width: 480px) {
      font-size: 1.2rem;
      line-height: 1.2;
    }

    @media (min-width: 1025px) {
      font-weight: 500;
      font-size: 1.6rem;
      line-height: 1.5;
    }
  }

  &:hover &__name {
    opacity: 1;
  }
}