.login-form {
  display: table;
  margin: 0 auto;
  width: 260px;
  max-width: 100%;
  text-align: center;

  .input-text {
    text-align: center;
  }

  &__logo {
    margin-bottom: 25px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (max-width: 640px) {
      img {
        height: 80px;
      }
    }

    @media (min-width: 1025px) and (max-width: 1200px) {
      margin-bottom: 50px;
    }

    @media (min-width: 1201px) {
      margin-bottom: 115px;
    }
  }

  &_registration &__logo {
    @media (min-width: 1201px) {
      margin-bottom: 85px;
    }
  }

  &__form {
    width: 260px;
  }

  .btn {
    width: 100%;
  }

  &__links {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 45px;
    font-size: 1.4rem;
    line-height: 1.2;
    letter-spacing: -0.28px;
    color: $color-silver-chalice;

    a {
      @include link-border($color-white);
      flex: 1 1 auto;
    }
  }

  &__link {
    margin-right: 7px;
  }

  &_registration .input_submit {
    @media (min-width: 1025px) {
      margin-top: 45px;
    }
  }
}