.home-mission {
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: stretch;
  letter-spacing: 0;
  width: 100%;
  height: 100%;
  background: #000;

  &__header {
    flex: 0 0 auto;
  }

  &__nav {
    margin: 0;
    padding: 25px 0;
    font-size: 1.4rem;
    line-height: 1;
    color: $color-alabaster;
    background-color: $color-tamarillo;
    background-image: url("../images/pic/home/bg_coat_of_arms.png");
    background-position: center bottom -40px;
    background-repeat: no-repeat;
    background-size: 130% auto;

    @media (min-width: 1025px) {
      background-position: right bottom -400px;
      background-size: auto;
    }

    @media (min-width: 1025px) {
      padding: 35px 0;
      font-size: 1.6rem;
    }

    &-item {
      display: inline-block;
      vertical-align: middle;
      opacity: 0.5;
      margin: 0 15px;
      transition: opacity 0.3s ease;

      @media (min-width: 1025px) {
        margin: 0 25px;
      }

      &_arrow {
        opacity: 0.2;
        width: 14px;
        height: 21px;

        svg {
          max-width: 100%;
          max-height: 100%;
          stroke: $color-silver;
        }
      }

      &:first-child {
        margin-left: 0;
        font-weight: 500;
      }

      a {
        text-decoration: none;
      }

      &.is-active,
      &:hover {
        opacity: 1;
      }
    }
  }

  &__gallery {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 21;
    margin: auto;
    transform: translateX(100%);
    transform-origin: 0 50%;
    transition: transform 1s ease;

    &.is-open {
      transform: translateX(0);
    }
  }

  &__inner {
    flex: 1 1 auto;
    position: relative;
    z-index: 2;
    overflow: hidden;

    &::before {
      @media (min-width: 1025px) {
        content: "";
        position: absolute;
        right: 0;
        top: 50%;
        z-index: 1;
        width: 30%;
        height: 120%;
        background: radial-gradient(farthest-side at right, $color-red, rgba($color-white, 0));
        transform: translateY(-50%);
      }

      @media (min-width: 1681px) {
        width: 60%;
      }
    }
  }

  &__content {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 6.7%;
    padding-bottom: 6.7%;
    height: 100%;
    background-position: left -240px top;
    background-repeat: no-repeat;
    background-size: contain;
    transform: translateX(0);
    transform-origin: 0 50%;
    transition: transform 1s ease;

    &.is-close {
      transform: translateX(-100vw);
    }

    @media (max-width: 1024px) {
      background-image: url("../images/pic/home/floid_face.png") !important;
      background-size: auto 100%;
    }

    @media (min-width: 1401px) {
      align-items: flex-end;
    }

    @media (min-width: 1025px) and (max-width: 1680px) {
      background-position: right bottom;
    }

    @media (min-width: 1681px) {
      background-position: right 175px top;
    }
  }

  &__descr {
    position: relative;
    z-index: 2;
    padding: 0 0 15px;
    max-width: 800px;
    font-weight: 300;
    font-size: 1.4rem;
    line-height: 1.5;

    @media (min-width: 321px) and (max-width: 768px) {
      position: relative;
      padding-top: 190px;
      font-style: italic;
      background: url("../images/logo_coat_of_arms.png") no-repeat left top 25px;
    }

    @media (min-width: 1025px) {
      font-weight: 400;
      line-height: 2;
    }

    @media (min-width: 1401px) {
      max-width: 690px;
    }

    q {
      display: block;
      margin-bottom: 1.5em;
      font-style: normal;
      font-size: 1.4rem;
      line-height: 1.5;
      color: $color-white;

      @media (min-width: 1025px) {
        font-weight: 500;
        font-size: 1.9rem;
        line-height: 1.55;
      }

      @media (min-width: 1025px) and (max-width: 1440px) {
        margin-bottom: 2em;
      }

      @media (min-width: 1441px) {
        margin-bottom: 2.4em;
      }
    }

    p {
      margin-bottom: 1em;

      @media (min-width: 1025px) and (max-width: 1440px) {
        margin-bottom: 1.5em;
      }

      @media (min-width: 1441px) {
        margin-bottom: 2em;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__more {
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 20;
    padding: 0 200px 55px 35px;
    width: 100%;
    height: 100px;
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 45px;
    color: #666;
    text-decoration: none;
    background: #000;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      width: 100%;
      height: 145px;
      background: linear-gradient(1deg, $color-black 0%, rgba(0, 0, 0, 0) 100%);
    }

    span {
      position: relative;
      z-index: 2;
      display: inline-block;
      vertical-align: bottom;
      line-height: 1;
      stroke: $color-red;
    }

    svg {
      display: inline;
      vertical-align: top;
      margin-top: 1px;
      margin-right: 15px;
      width: 13px;
      height: 10px;
      color:$color-red;
    }

    @media (min-width: 769px) {
      display: none;
    }
  }

  &__signature {
    @media (max-width: 768px) {
      display: block;
      position: absolute;
      right: 35px;
      bottom: 30px;
      z-index: 21;
      width: 105px;
      height: 65px;
    }
  }
}