.logo {
  display: block;
  margin: 0 auto;
  width: 60px;
  max-width: 100%;
  text-decoration: none;
  transition: width 0.3s;

  @media (min-width: 769px) and (max-width: 1024px) {
    width: 100px;
  }

  @media (min-width: 1025px) and (max-width: 1680px) {
    width: 140px;
  }

  @media (min-width: 1681px) {
    width: 185px;
  }

  .is-fixed & {
    width: 60px;
  }

  img {
    display: block;
    margin: 0 auto;
  }

  &_horizontal {
    margin: 0;
    width: 238px;
  }
}