.gallery {
  height: 100%;
  background: #000;

  &__item {
    display: flex !important;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: stretch;
    float: left;
    height: 100%;
  }

  .slick-list,
  .slick-track {
    height: 100%;
  }

  .slick-arrow {
    bottom: auto;
    top: 50%;
    margin-top: -15px;

    &:not(.is-disabled) svg {
      opacity: 1;
    }
  }
}