.tabs {
  margin: 25px -5px;
  font-size: 1.4rem;
  letter-spacing: -0.28px;
  color: $color-white;

  @media (min-width: 769px) and (max-width: 1024px) {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  @media (min-width: 1025px) {
    margin-top: 65px;
    margin-bottom: 65px;
  }

  &__item {
    display: inline-block;
    vertical-align: middle;
    overflow: hidden;
    margin: 5px;
    border-radius: 10px;
    width: 90px;
    height: 34px;
    line-height: 34px;
    text-align: center;
    text-decoration: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: $color-white;
    background: #740f0f;
    transition: background 0.3s ease;

    @media (min-width: 1025px) {
      width: 115px;
    }

    &:hover,
    &.is-logout,
    &.is-active {
      background: $color-guardsman-red;
    }
  }
}