.promo-form {
  display: table;
  width: 100%;

  @media (min-width: 481px) {
  }

  .input-text,
  select,
  .input-textarea,
  .input-placeholder {
    width: 100%;
    text-align: left;
  }

  &__input,
  &__btn {
    display: table-cell;
    vertical-align: middle;
    padding: 0 8px;

    @media (min-width: 481px) {
    }

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  &__input {
    .input-text {
      background: #121212;
    }
  }

  &__btn {
    .btn_big {
      width: 138px;

      @media (max-width: 480px) {
        padding: 0 5px;
        width: 88px;
        font-size: 1.2rem;
      }
    }
  }
}