.main-menu {
  font-size: 1.4rem;
  line-height: 1.2;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  li {
    margin: 15px 0;
    padding: 0;

    @media (min-width: 769px) and (max-width: 1024px) {
      margin: 20px 0;
    }

    @media (min-width: 1025px) and (max-width: 1440px) {
      margin: 25px 0;
    }

    @media (min-width: 1441px) {
      margin: 45px 0;
    }

    a {
      opacity: 0.7;
      text-decoration: none;
      transition: opacity 0.2s ease;
    }

    &.is-active a,
    & a:hover {
      opacity: 1;
    }

    ul {
      display: none;
    }
  }
}