.home-academy {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-end;
  align-items: flex-start;
  position: relative;
  padding-top: 40px;
  padding-bottom: 100px;
  height: 100%;
  letter-spacing: 0;

  @media (min-width: 1201px) and (max-width: 1400px) {
    margin-left: auto;
    margin-right: auto;
    max-width: 1201px;
  }

  &__inner {
    width: 100%;

    @media (min-width: 481px) and (max-width: 768px) and (min-height: 813px) {
      width: 65%;
    }

    @media (min-width: 769px) and (max-width: 1200px) {
      width: 75%;
    }

    @media (min-width: 1201px) {
      width: 42.5%;
    }
  }

  &__title {
    display: inline-block;
    vertical-align: top;
    position: relative;
    margin: 0;
    padding: 0 120px 0 0;
    font-weight: 700;
    font-size: 3rem;
    line-height: 0.95;
    text-transform: uppercase;
    color: $color-alabaster;

    &::before {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      margin: -58px 0 0 0;
      opacity: 0.5;
      width: 119px;
      height: 113px;
      background: url("../images/logo_academy.png") no-repeat left center;

      @media (min-width: 769px) {
        left: 0;
        right: auto;
        opacity: 1;
      }
    }

    @media (min-width: 769px) {
      padding-right: 0;
      padding-left: 190px;
      font-size: 3.8rem;
    }

    @media (min-width: 1025px) {
      margin-left: 30px;
      font-size: 4.8rem;
      line-height: 1.05;
    }
  }

  &__content {
    overflow: hidden;
    margin-top: 40px;
    margin-bottom: 15px;
    font-size: 1.4rem;
    line-height: 1.5;

    @media (max-height: 812px) {
      max-height: 10.25em;
      font-size: 1.2rem;
    }

    @media (min-width: 769px) and (min-height: 813px) {
      max-height: none;
    }

    @media (min-width: 769px) and (max-width: 1024px) {
      margin-top: 40px;
      margin-bottom: 40px;
    }

    @media (min-width: 1025px) {
      margin-top: 90px;
      margin-bottom: 90px;
      line-height: 2;

      p {
        margin-bottom: 2em;
      }
    }

    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__btn {
    position: absolute;
    bottom: 0;
    left: 35px;
    right: 35px;
    height: 65px;
    font-weight: 500;
    font-size: 1.8rem;
    text-align: center;
    text-decoration: none;
    color: $color-white;

    @media (min-width: 769px) {
      left: auto;
      right: auto;
      height: 75px;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      bottom: 0;
      z-index: 1;
      margin-left: -100%;
      width: 200%;
      height: 120%;
      background: radial-gradient(farthest-side at bottom, $color-red, rgba($color-white, 0));
      transition: height 0.3s ease;
    }

    &:hover {
      &::before {
        height: 160%;
      }
    }

    span {
      position: relative;
      z-index: 2;
    }
  }
}