.tpl-header {
  position: relative;
  background: $color-black;

  &.is-fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 100;
  }

  &__inner {
    position: relative;
    padding-top: 18px;
    padding-bottom: 18px;

    @media (min-width: 1025px) {
      padding-top: 35px;
      padding-bottom: 35px;
    }

    @media (min-width: 1681px) {
      position: static;
    }
  }

  &.is-fixed &__inner {
    padding-top: 18px;
    padding-bottom: 18px;
  }

  &__hamburger {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;

    &-wrap {
      position: static;
    }
  }

  &__lang-bar {
    text-align: right;
  }
}