.pagination {
  padding: 10px 15px;
  font-weight: 400;
  font-size: 1.6rem;
  letter-spacing: -0.96px;
  text-align: center;
  color: $color-alabaster;
  background: $color-tamarillo;

  @media (min-width: 769px) and (max-width: 1024px) {
    padding: 15px 20px;
  }

  @media (min-width: 1025px) and (max-width: 1440px) {
    padding: 20px 40px;
  }

  @media (min-width: 1441px) {
    padding: 22px 50px;
  }

  &_loader {
    padding: 5px 15px;
    font-size: 2.1rem;
    line-height: 1.5;
    letter-spacing: 0;
    color: rgba($color-alto, 0.6);
    transition: color 0.3s;

    @media (min-width: 769px) and (max-width: 1024px) {
      padding: 20px 20px;
    }

    @media (min-width: 1025px) and (max-width: 1440px) {
      padding: 25px 40px;
    }

    @media (min-width: 1441px) {
      padding: 35px 50px;
    }

    &:not(.is-loader) {
      padding: 0;
    }

    a {
      display: block;
      padding: 10px 15px;
      width: 100%;

      @media (min-width: 769px) and (max-width: 1024px) {
        padding: 25px 20px;
      }

      @media (min-width: 1025px) and (max-width: 1440px) {
        padding: 30px 40px;
      }

      @media (min-width: 1441px) {
        padding: 40px 50px;
      }
    }

    &.is-loader a {
      display: none;
    }

    &:hover {
      color: $color-white;
    }
  }

  a {
    text-decoration: none;
  }

  &__prev,
  &__next {
    padding: 0;

    @media (min-width: 1025px) {
      padding: 5px 0;
    }

    svg {
      display: inline-block;
      vertical-align: middle;
      width: 15px;
      height: 20px;
      stroke: $color-silver;
      transition: all 0.3s ease;

      @media (min-width: 1025px) {
        width: 20px;
        height: 30px;
      }

      @media (max-width: 1024px) {
        &:nth-child(2),
        &:nth-child(3) {
          display: none;
        }
      }
    }

    &:hover svg {
      stroke: #fff;
    }

    &.is-disabled {
      cursor: not-allowed;

      svg {
        opacity: 1;
        stroke: $color-gallery;
      }

      svg:nth-child(2),
      svg:nth-child(3) {
        display: none;
      }
    }
  }

  &__prev {
    float: left;

    svg {
      margin-left: -5px;
    }

    svg:nth-child(2) {
      opacity: 0.5;
    }

    svg:nth-child(3) {
      opacity: 0.1;
    }
  }

  &__next {
    float: right;

    svg {
      margin-right: -5px;
    }

    @media (min-width: 1025px) {
      svg {
        opacity: 0.1;
      }

      svg:nth-child(2) {
        opacity: 0.5;
      }

      svg:nth-child(3) {
        opacity: 1;
      }
    }
  }

  &__list {
    margin: 0 55px;
  }

  &__item {
    display: inline-block;
    vertical-align: middle;
    opacity: 0.6;
    margin: 0 5px;
    padding: 5px;
    transition: opacity 0.3s ease;

    @media (min-width: 769px) and (max-width: 1024px) {
      margin: 0 15px;
    }

    @media (min-width: 1025px) and (max-width: 1440px) {
      margin: 0 25px;
    }

    @media (min-width: 1441px) {
      margin: 0 32px;
    }

    &:hover,
    &.is-active {
      opacity: 1;
    }
  }
}