.map-baloon {
  position: absolute;
  margin: -15px 0 0 0;
  width: 170px;
  transform: translateY(-100%);

  &__block {
    position: relative;
    border-radius: 11px;
    padding: 20px;
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    color: $color-gallery;
    background: $color-guardsman-red;

    &::after {
      content: "";
      display: block;
      position: absolute;
      bottom: -30px;
      left: 50%;
      margin-left: -11px;
      border-width: 15px 11px;
      border-style: solid;
      border-color: transparent;
      border-top-color: $color-guardsman-red;
    }
  }

  &__close {
    position: absolute;
    right: -10px;
    top: -15px;
    font-size: 20px;
    line-height: 1;
    cursor: pointer;
  }

  &__title {
    margin: 0 0 5px;
    font-weight: 700;
    font-size: 2.1rem;
    line-height: 1.1;
  }

  &__content {
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.2;
  }

  a {
    display: inline-block;
    vertical-align: middle;
    margin: 2px 0;
    text-decoration: none;

    svg {
      display: inline;
      vertical-align: middle;
      margin-right: 7px;
      width: 14px;
      height: 14px;
      fill: #fff;
    }
  }

  &__instagram {

  }
}