.home {
  &__section {
    position: relative;

    &_catalog {
      overflow: hidden;
      background: #1a1a1a;

      @media (min-width: 1201px) {
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          z-index: 1;
          width: 100%;
          height: 100%;
          background: url("../images/bg_logo.png") no-repeat center top 50px;
          background-size: auto 80%;
        }
      }
    }

    &_news {
      overflow: hidden;
    }
    
    &_academy {
      position: relative;
      background-image: url("../images/pic/home/academy_mobile.jpg");
      background-repeat: no-repeat;
      background-position: center top;
      background-size: cover;
      
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        background: $color-black;
        background: linear-gradient(15deg, var(--color-black) 0%, rgba(0, 0, 0, 0) 100%);

        @media (min-width: 375px) {
          background: linear-gradient(to right, $color-black 0%, rgba(0, 0, 0, 0) 100%);
        }
      }

      .fp-tableCell {
        vertical-align: bottom;
      }

      @media (min-width: 481px) {
        background-image: url("../images/pic/home/academy.jpg");
      }
    }
  }

  &__section-parallax {
    display: none;

    @media (min-width: 1025px) {
      position: absolute;
      display: block;
      right: -15%;
      bottom: -60%;
      z-index: 2;
      max-height: 80%;
      max-width: 40%;
      transform: none;
      transition: transform 0.01s linear;
    }
  }

  &__inner {
    position: relative;
    z-index: 2;
  }

  &__section_catalog &__inner {
    overflow: hidden;
  }
}