.news {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;

  .home__section & {
    @media (min-height: 768px) {
      height: 100%;
    }
  }

  &__item {
    float: left;
    width: 100%;

    @media (min-width: 641px) and (max-width: 1270px) {
      width: 50%;
    }

    @media (min-width: 1271px) {
      width: 33.33%;
    }

    .home__section & {
      @media (min-width: 1025px) {
        width: 33.33%;
      }

      @media (min-height: 768px) {
        height: 50%;
      }
    }
  }

  &__more {
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    bottom: 0;
    left: 35px;
    right: 35px;
    margin: auto;
    width: 185px;
    height: 40px;
    font-weight: 400;
    font-size: 1.4rem;
    text-align: center;
    text-decoration: none;
    color: $color-white;

    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      bottom: -170px;
      z-index: 1;
      border-radius: 50%;
      margin-left: -55%;
      width: 110%;
      height: 70px;
      box-shadow: 0 -140px 60px 40px rgba(255, 0, 0, 0.5);
      transition: all 0.3s ease;
    }

    @media (min-width: 769px) {
      left: 66.66%;
      right: 0;
      bottom: auto;
      top: 0;
      margin: auto;
      padding-top: 50px;
      width: 245px;
      font-size: 1.8rem;
      color: $color-emperor;

      &::before {
        bottom: auto;
        top: -170px;
        box-shadow: 0 70px 20px 60px rgba(255, 0, 0, 0.75);
      }
    }

    &:hover {
      &::before {
        box-shadow: 0 -140px 50px 40px rgba(255, 0, 0, 0.5);
      }

      @media (min-width: 769px) {
        color: $color-emperor;

        &::before {
          box-shadow: 0 80px 20px 60px rgba(255, 0, 0, 0.75);
        }
      }
    }

    span {
      position: relative;
      z-index: 2;
      opacity: 0.8;

      &.is-desktop {
        display: none;
      }

      @media (min-width: 769px) {
        &.is-mobile {
          display: none;
        }

        &.is-desktop {
          display: block;
        }
      }
    }
  }
}