.slick {
  &-arrow {
    position: absolute;
    bottom: 25px;
    z-index: 100;
    white-space: nowrap;
    cursor: pointer;

    @media (min-width: 1025px) {
      bottom: 75px;
    }

    @media (min-width: 1025px) and (max-height: 1024px) {
      bottom: 30px;
    }

    svg {
      display: inline-block;
      vertical-align: middle;
      width: 20px;
      height: 30px;
      stroke: #7e7e7e;

      @media (max-width: 1024px) {
        &:nth-child(2),
        &:nth-child(3) {
          display: none;
        }
      }

      @media (min-width: 1025px) {
        stroke: $color-silver;
      }
    }

    &.slick-prev {
      left: 25px;

      @media (min-width: 1025px) and (max-width: 1799px) {
        left: 35px;
      }

      @media (min-width: 1800px) {
        left: 50%;
        margin-left: -915px;
      }

      svg:nth-child(2) {
        opacity: 0.5;
      }

      svg:nth-child(3) {
        opacity: 0.1;
      }
    }

    &.slick-next {
      right: 25px;

      @media (min-width: 1025px) and (max-width: 1799px) {
        right: 35px;
      }

      @media (min-width: 1800px) {
        right: 50%;
        margin-right: -915px;
      }

      @media (min-width: 1025px) {
        svg {
          opacity: 0.1;
        }

        svg:nth-child(2) {
          opacity: 0.5;
        }

        svg:nth-child(3) {
          opacity: 1;
        }
      }
    }

    &.swiper-button-disabled,
    &.slick-disabled {
      cursor: not-allowed;

      svg {
        opacity: 1;
        stroke: $color-gallery;
      }

      svg:nth-child(2),
      svg:nth-child(3) {
        display: none;
      }
    }
  }

  &-dots {
    position: absolute;
    left: 85px;
    right: 85px;
    bottom: 30px;
    z-index: 100;
    list-style: none;
    margin: 0 auto;
    padding: 0;
    max-width: 1640px;
    text-align: center;

    @media (min-width: 1025px) {
      left: 120px;
      right: 120px;
      bottom: 80px;
      text-align: left;
    }

    @media (min-width: 1800px) {
      left: 0;
      right: 0;
    }

    @media (min-width: 1025px) and (max-height: 1024px) {
      bottom: 35px;
    }

    span,
    li {
      display: inline-block;
      vertical-align: middle;
      margin: 5px 15px;
      border-radius: 4px;
      border: 1px solid $color-alto;
      padding: 0;
      width: 12px;
      height: 12px;
      background: $color-white;
      cursor: pointer;
      transition: border 0.3s ease, background 0.3s;

      @media (min-width: 1025px) {
        margin-left: 25px;
        margin-right: 25px;
      }

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }

      &:hover,
      &.slick-active,
      &.swiper-pagination-bullet-active {
        border-color: $color-silver-chalice;
        background: $color-silver-chalice;
      }
    }
  }
}