.start-page {
  display: table;
  table-layout: fixed;
  width: 100%;
  height: 100%;

  &__header,
  &__slider {
    display: table-row;
    width: 100%;
  }

  &__header {
    height: 3%;
  }

  &__slider {
    overflow: hidden;
    position: relative;
    height: 97%;

    @media (max-width: 768px) {
      overflow: hidden;
    }

    video {
      position: absolute;
      left: 50%;
      top: 50%;
      height: 100%;
      background: #000;
      transform-origin: 0 50%;
      transform: scale(1.2) translate(-50%, -50%);

      @media (orientation: landscape) and (min-width: 1201px) {
        left: 0;
        right: 0;
        margin: auto;
        width: 100%;
        height: auto;
        transform: scale(1.1) translate(0, -50%);
      }
    }
  }
}